@import url('https://fonts.googleapis.com/css?family=Roboto:400,900,900i');


*{
  padding:0;
  margin:0;
  list-style:none;
  line-height:1;
  // outline: 1px solid #f99;
  box-sizing:border-box;
}
$green : #50E3C2;
$title-size: 4.8rem;
$number-size: 4.8rem;
$desc-size:2.4rem;

body,html{
  font-family: 'Roboto',"Helvetica", "Arial","LiHei Pro","黑體-繁","微軟正黑體", sans-serif;
  font-weight:900;
  font-size: 10px;
}

img{
 max-width:100%; 
}
.subtitle{
  text-align:center;
  background:#000;
  color: #fff;
  font-size:2rem;
  line-height: 24px;
  width:131px;
  height: 27px;
  margin: 0 auto;

}
.title{
  text-align:center;
  font-size:7.2rem;
  line-height:85px;
}
.pic {
  img{
    vertical-align:middle;
  }
}



.wrap{
  display:grid;
  margin: 0 auto;
  max-width:960px;
  grid-template-columns: repeat(8,1fr);
  grid-template-rows: repeat(20,100px);
}
.item{
  position: relative;
  &:before,
  &:after{
    content:'';
    position: absolute;
    box-sizing:border-box;
    z-index: -20;
  }
}
// 綠色方框
.b20{
  border: 20px solid $green;
}
// 斜線
.stripes{
  width:240px;
  height:100px;
  background: repeating-linear-gradient(
    315deg,
    $green,
    $green 20px,
    transparent 20px,
    transparent 34px
  );
  background-position-x:25px;
  z-index: 10;
}

.item-content{
  position:relative;
  .itemTitle{
    font-size : $title-size;
    position : absolute;
    z-index: 30;
    &:before,
    &:after {
      content : attr(data-title);
      position: absolute;
      left:0;
      overflow: hidden;
    }
    &:before{
      top: -16px;
      opacity: .3;
      z-index: 20;
      height: 21px;
    }
    &:after {
      top:  -26px; 
      opacity: .1;
      z-index: 10;
      height: 14px;
    }
  }
  .desc{
    font-size:$desc-size;
    position : absolute;
    font-style: italic;
    width:fit-content;
  }
  .number{
   font-size:$number-size;
   position:absolute;
  }
}
.item0{
  grid-area: 1 / 1 / 1 / -1;
}
.item1{
  grid-area: 3 / 1 / span 4 / span 4;
  .itemTitle{
    top:-15px;
  }
  .number{
    bottom:-1em;
  }
  .desc{
    left:100%;
    top:75%;
    transform-origin:0 100%;
    transform: rotate(90deg);
  }
  &:before{
    @extend .stripes;
    bottom:-12.5%;
    left:12.5%;
    width:50%;
    height:25%;
  }
  &:after{
    @extend .b20;
    width:50%;
    height:50%;
    right:-12.5%;
    top:-12.5%;
  }
}
.item2{
  grid-area: 4 / 6 / span 2 / span 2;
  .itemTitle{
    top:calc(100% + 26px);
    left:50%;
  }
  .number{
    right:-50%;
    top:-50%;
  }
  .desc{
    right:50%;
    top: -1em;
  }
  &:before{
    z-index: -10;
    left:25%;
    top:-25%;
    width:100%;
    height:100%;
    background:$green;
  }
  &:after{
    @extend .b20;
    z-index: -20;
    left:50%;
    top:-50%;
    width:100%;
    height:100%;
  }
}
.item3{
  grid-area: 9 / 1 / span 2 / span 4;
  .itemTitle{
    bottom:100%;
    right:0;
  }
  .number{
    left:100%;
    top:100%;
  }
  .desc{
    right:50%;
    top: 100%;
  }
  &:before{
    z-index: -10;
    left:0%;
    top:-25%;
    width:50%;
    height:25%;
    background:$green;
  }
  &:after{
    @extend .b20;
    z-index: -20;
    left:62.5%;
    top:25%;
    width:50%;
    height:100%;
  }
}
.item4{
  grid-area: 9 / 7 / span 4 / span 2;
  .itemTitle{
    bottom:100%;
    right:100%;
    transform-origin:100% 100%;
    transform: rotate(-90deg);
  }
  .number{
    bottom:0;
    right:100%;
  }
  .desc{
    right:25%;
    bottom:100%;
    transform-origin:100% 50%;
    transform: rotate(90deg);
  }
  &:before{
    @extend .stripes;
    bottom:-12.5%;
    left:0%;
    width:100%;
    height:25%;
  }
  &:after{
    @extend .b20;
    width:100%;
    height:50%;
    right:25%;
    top:-25%;
  }
}
.item5{
  grid-area: 13 / 3 / span 2 / span 2;
  .itemTitle{
    bottom:50%;
    left:-100%;
  }
  .number{
    bottom:0;
    left:-50%;
  }
  .desc{
    left:100%;
    top:0%;
  }
  &:before{
    @extend .stripes;
    bottom:75%;
    right:75%;
    width:100%;
    height:50%;
  }
  &:after{
    @extend .b20;
    width:100%;
    height:50%;
    right:50%;
    top:75%;
  }
}
.item6{
  grid-area: 15 / 5 / span 4 / span 4;
  .itemTitle{
    right:0%;
    bottom:100%;
  }
  .number{
    bottom:50%;
    right:100%;
  }
  .desc{
    left:0%;
    top:100%;
    text-align:center;
    width:100%;
  }
  &:before{
    background:$green;
    bottom:0%;
    left:-12.5%;
    width:12.5%;
    height:50%;
  }
  &:after{
    @extend .b20;
    width:100%;
    height:25%;
    left:0%;
    top:87.5%;
  }  
}
.item7{
  grid-area: 17 / 1 / span 4 / span 2;
  .itemTitle{
    left:100%;
    bottom:100%;
    transform-origin:0% 100%;
    transform: rotate(90deg);
  }
  .number{
    bottom:0%;
    right:-50%;
  }
  .desc{
    left:0%;
    bottom:100%;
  }
  &:before{
    background:$green;
    bottom:0%;
    right:-50%;
    width:50%;
    height:50%;
  }
  &:after{
    @extend .b20;
    width:100%;
    height:75%;
    left:25%;
    top:-12.5%;
  }  
}


.footer{
  text-align:center;
  background:#f1f1f1;
  padding:15px;
  margin-top: 30px

}
